#water {
  background-color: #9cc6ff;
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100vw;
  height: 100vh;
  -webkit-transition: all 3s ease-out;
  -moz-transition: all 3s ease-out;
  -o-transition: all 3s ease-out;
  transition: all 3s ease-out;
}

#button-area {
  display: flex;
  align-content: center;
  justify-content: center;
  position: absolute;
  height: 100vh;
  width: 100vw;
  left: 0;
  top: 0;
}

#drink-button {
  height: 94vh;
  width: 16vw;
  margin: 3vh;
  font-size: 1.5rem;
}

#spinner-area {
  display: flex;
  align-content: center;
  justify-content: center;
}